* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  background: #fff;
}

.container {
  width: 360px;
  margin: 20px auto;
  padding-bottom: 15px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 35px 45px auto;
  row-gap: 10px;
  background-color: #f7f7f7;
}

.header {
  grid-column: 1 / 3;
  grid-row: 1/ 2;
  text-align: center;
  width: 320px;
  height: 35px;
  background: #8b90f8;
  font-weight: bold;
  line-height: 35px;
  text-transform: uppercase;
  color: #fff;
  -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
}

.add-todo {
  grid-column: 1 / 3;
  grid-row: 2 / 3;
  padding: 5px 15px 0px 15px;
}

#todo-form {
  display: flex;
}

.add-todo input {
  flex-grow: 1;
  padding: 5px;
  height: 35px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-width: 2px;
  border-color: #38ef7d;
  outline: none;
}

.add-todo button {
  height: 35px;
  background: #38ef7d;
  border: none;
  text-transform: uppercase;
  padding: 0px 15px;
  font-weight: bold;
  color: #333;
}

.todo-container {
  grid-column: 1 / 3;
  grid-row: 3 / 4;
  padding: 0px 15px;
}

.todo-container > div > div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: auto;
  grid-gap: 15px;
}
