body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  background: #fff;
}

.container {
  width: 360px;
  margin: 20px auto;
  padding-bottom: 15px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 35px 45px auto;
  grid-row-gap: 10px;
  row-gap: 10px;
  background-color: #f7f7f7;
}

.header {
  grid-column: 1 / 3;
  grid-row: 1/ 2;
  text-align: center;
  width: 320px;
  height: 35px;
  background: #8b90f8;
  font-weight: bold;
  line-height: 35px;
  text-transform: uppercase;
  color: #fff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
}

.add-todo {
  grid-column: 1 / 3;
  grid-row: 2 / 3;
  padding: 5px 15px 0px 15px;
}

#todo-form {
  display: flex;
}

.add-todo input {
  flex-grow: 1;
  padding: 5px;
  height: 35px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-width: 2px;
  border-color: #38ef7d;
  outline: none;
}

.add-todo button {
  height: 35px;
  background: #38ef7d;
  border: none;
  text-transform: uppercase;
  padding: 0px 15px;
  font-weight: bold;
  color: #333;
}

.todo-container {
  grid-column: 1 / 3;
  grid-row: 3 / 4;
  padding: 0px 15px;
}

.todo-container > div > div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-auto-rows: auto;
  grid-gap: 15px;
}

.todo-item {
  grid-column: 1 / 3;
  display: flex;
  padding: 8px;
  border-left: 2px solid #38ef7d;
  background: #fff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
}

.todo-item p {
  flex-grow: 1;
}

.checked p {
  text-decoration: line-through;
}

.hidden {
  display: none;
}

.editInput {
  width: 167px;
  margin-right: 25px;
  padding: 1px 5px;
  border: none;
  border-bottom: 1px solid #38ef7c;
  outline: none;
}

.todo-item input[type="checkbox"] {
  vertical-align: bottom;
  position: relative;
  top: 4px;
  *overflow: hidden;
  margin-right: 5px;
}

.btns-wrap {
  width: 80px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.todo-item div button {
  width: 35px;
}

.todo-item button {
  height: 20px;
}

.delete,
.cancel {
  border: 1px solid #fe846c;
  background: none;
}

.edit {
  border: 1px solid #807bfd;
  background: none;
}

.edit-icon {
  color: #807bfd;
}

.trash-icon,
.cancel-icon {
  color: #fe846c;
}

.save {
  border: 1px solid #38ef7c;
  background: none;
}

.save-icon {
  color: #38ef7c;
}

