.todo-item {
  grid-column: 1 / 3;
  display: flex;
  padding: 8px;
  border-left: 2px solid #38ef7d;
  background: #fff;
  -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
}

.todo-item p {
  flex-grow: 1;
}

.checked p {
  text-decoration: line-through;
}

.hidden {
  display: none;
}

.editInput {
  width: 167px;
  margin-right: 25px;
  padding: 1px 5px;
  border: none;
  border-bottom: 1px solid #38ef7c;
  outline: none;
}

.todo-item input[type="checkbox"] {
  vertical-align: bottom;
  position: relative;
  top: 4px;
  *overflow: hidden;
  margin-right: 5px;
}

.btns-wrap {
  width: 80px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.todo-item div button {
  width: 35px;
}

.todo-item button {
  height: 20px;
}

.delete,
.cancel {
  border: 1px solid #fe846c;
  background: none;
}

.edit {
  border: 1px solid #807bfd;
  background: none;
}

.edit-icon {
  color: #807bfd;
}

.trash-icon,
.cancel-icon {
  color: #fe846c;
}

.save {
  border: 1px solid #38ef7c;
  background: none;
}

.save-icon {
  color: #38ef7c;
}
